.about {
  margin-top: 100px;
}

.about__content {
  display: flex;
  align-items: center;
  padding: 15px 0;
  border-top: 5px solid #373645;
  border-bottom: 5px solid #6e9bca;
}

.about__content .content__title {
  margin-right: 20px;
}

.block__icon {
  margin-bottom: 35px;
  max-width: 290px;
  max-height: 290px;
}

.block__icon-img {
  max-height: 100%;
  max-width: 100%;
}

.block__title {
  color: #373645;
  font-size: 20px;
  margin-bottom: 15px;
}

.block__subtitle {
  font-size: 12px;
  color: #cccccc;
  margin-bottom: 25px;
}

.block__button {
  text-align: right;
  /*margin-top: auto;*/
}

.burger {
  display: inline-block;
  height: 25px;
  width: 25px;
  cursor: pointer;
  position: fixed;
  top: 60px;
  right: 15px;
  z-index: 10;
  opacity: 0;
  visibility: hidden;
}

.burger__line {
  display: block;
  height: 6px;
  width: 100%;
  background-color: black;
  border: 1px solid #fff;
  border-radius: 3px;
  margin-bottom: 3px;
  transition: .5s;
}

.burger__line:last-child {
  margin-bottom: 0;
}

.lines .burger__line {
  background-color: black;
  transform-origin: left 50%;
}

.lines .burger__line:nth-child(1) {
  transform: rotate(45deg);
}

.lines .burger__line:nth-child(2) {
  visibility: hidden;
  opacity: 0;
}

.lines .burger__line:nth-child(3) {
  transform: rotate(-45deg);
}

.clients {
  margin-top: 50px;
}

.clients__content {
  text-align: center;
}

.clients__list {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 50px;
}

.clients__item {
  margin-right: 30px;
}

.clients__item:last-child {
  margin-right: 0;
}

.clients__link {
  display: block;
}

.clients__link--tvdigital:before,
.clients__link--changes:before,
.clients__link--finance:before,
.clients__link--lezo:before {
  content: '';
  display: block;
  height: 50px;
  width: 130px;
  background-position: center center;
  background-repeat: no-repeat;
  -webkit-background-size: contain;
  background-size: contain;
}

.clients__link--tvdigital:before {
  background-image: url(../img/clients/1.png);
}

.clients__link--changes:before {
  background-image: url(../img/clients/2.png);
}

.clients__link--finance:before {
  background-image: url(../img/clients/3.png);
}

.clients__link--lezo:before {
  background-image: url(../img/clients/4.png);
}

.content {
  text-transform: uppercase;
}

.content__title {
  position: relative;
  color: #373645;
  font-size: 30px;
  font-weight: 900;
  overflow: hidden;
}

.content__title:after,
.content__title:before {
  content: '';
  display: inline-block;
  vertical-align: middle;
  width: 100%;
  height: 2px;
  background-color: #ccc;
}

.content__title:before {
  margin-left: -100%;
  margin-right: 25px;
}

.content__title:after {
  margin-right: -100%;
  margin-left: 25px;
}

.content__description {
  font-weight: 300;
  font-size: 21px;
  color: #cccccc;
}

.copyright__title {
  text-transform: uppercase;
  font-weight: 900;
  color: #6e9bca;
  margin-bottom: 10px;
}

.copyright__info {
  font-size: 12px;
  color: #ffffff;
  margin-bottom: 10px;
}

.footer {
  background-color: #373645;
  margin-top: 50px;
  padding: 25px 0;
}

.footer__row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer__row--right {
  margin-top: 20px;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

.footer__copyright {
  width: 25%;
}

.footer__content {
  width: 100%;
  text-align: center;
}

.footer__content .content__title {
  color: #fff;
}

.footer__content .content__title:after,
.footer__content .content__title:before {
  display: none;
}

.footer__list {
  display: flex;
  justify-content: space-between;
  margin-top: 50px;
  width: 100%;
  padding: 0 10%;
}

.footer__city {
  font-size: 12px;
  font-weight: 300;
  color: #fff;
}

.footer__city:last-child {
  margin-right: 0;
}

.footer__city-name {
  text-transform: uppercase;
  color: #cccccc;
  font-weight: 900;
  margin-bottom: 10px;
}

.footer__city-adress {
  margin-bottom: 10px;
}

.footer__city-contacts_item {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.footer__city-contacts_item:last-child {
  margin-bottom: 0;
}

.footer__city-contacts_name {
  color: #6e9bca;
  font-weight: 900;
  margin-right: 10px;
}

.fullscreen {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: #373645;
  z-index: 5;
  opacity: 0;
  visibility: hidden;
  overflow-y: hidden;
  display: flex;
  align-items: center;
}

.fullscreen__menu {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.fullscreen__menu .menu__list {
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.fullscreen__menu .menu__item {
  margin-right: 0;
  margin-bottom: 20px;
  opacity: .5;
  transition: 1s;
  transition-delay: .1s;
  transform: translateX(-150%);
}

.fullscreen__menu .menu__item:last-child {
  margin-bottom: 0;
}

.fullscreen__menu .menu__item:nth-child(even) {
  transform: translateX(150%);
}

.fullscreen__menu .menu__link {
  color: #fff;
  font-size: 30px;
}

.animate .menu__item {
  opacity: 1;
  transform: translateX(0);
}

.animate .menu__item:nth-child(even) {
  transform: translateX(0);
}

.is-active {
  visibility: visible;
  opacity: 1;
}

.header {
  background-color: #fff;
  display: flex;
  align-items: center;
  z-index: 3;
  width: 100%;
}

.header__logo {
  background-color: #373645;
  padding: 30px 35px;
  margin-right: 30px;
}

.header__menu {
  margin-right: 40px;
}

.header__socials {
  margin-right: 30px;
}

.header__burger {
  display: none;
  height: 25px;
  width: 25px;
  margin-left: auto;
  margin-right: 30px;
  position: relative;
}

.fixed {
  position: fixed;
  top: 0;
  left: 0;
}

.logo__link {
  display: inline-block;
}

.logo__img {
  max-width: 100%;
  max-height: 100%;
}

.main {
  background: url(../img/main-bg.png) 53% 50% no-repeat;
  -webkit-background-size: cover;
  background-size: cover;
  padding-top: 20px;
}

.main__slider {
  margin: 100px 0;
}

.menu__list {
  display: flex;
  align-items: center;
}

.menu__item {
  margin-right: 25px;
}

.menu__item:last-child {
  margin-right: 0;
}

.menu__link {
  color: #373645;
  font-weight: 400;
  text-transform: uppercase;
}

.menu__link:hover {
  color: blue;
}

.news {
  margin-top: 50px;
}

.news__content {
  text-align: center;
}

.news__list {
  display: flex;
  margin-top: 50px;
  padding: 10px;
  background: url(../img/lines-bg.png) center center no-repeat;
  -webkit-background-size: cover;
  background-size: cover;
}

.news__item {
  display: flex;
  margin-right: 30px;
  flex: 1;
  background-color: #fff;
}

.news__item:last-child {
  margin-right: 0;
}

.news__col {
  width: 15%;
  margin-right: 20px;
  text-align: center;
}

.news__col--user {
  flex: 1;
  text-align: left;
}

.news__col:last-child {
  margin-right: 0;
}

.news__date {
  font-weight: 300;
  text-transform: uppercase;
  margin-bottom: 20px;
  border-right: 1px solid #ccc;
}

.news__date-month {
  font-size: 25px;
  color: #373645;
}

.news__date-number {
  font-weight: 800;
  font-size: 50px;
  color: #6e9bca;
}

.news__date-year {
  font-size: 15px;
  color: #cccccc;
}

.news__communications {
  border-right: 1px solid #ccc;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.news__communications-likes,
.news__communications-share,
.news__communications-comments {
  position: relative;
  display: inline-block;
}

.news__communications-likes:before,
.news__communications-share:before,
.news__communications-comments:before {
  content: '';
  display: inline-block;
  width: 30px;
  height: 30px;
  background-repeat: no-repeat;
  background-position: center center;
}

.news__communications-likes:after,
.news__communications-share:after,
.news__communications-comments:after {
  content: '';
  border-radius: 50%;
  background-color: #373645;
  width: 15px;
  height: 15px;
  display: inline-block;
  position: absolute;
  right: -5px;
  top: 0;
  font-weight: 300;
  color: #fff;
  font-size: 8px;
  line-height: 15px;
  vertical-align: middle;
}

.news__communications-likes:before {
  background-image: url(../img/reviews/like.png);
}

.news__communications-likes:after {
  content: '12';
}

.news__communications-share:before {
  background-image: url(../img/reviews/share.png);
}

.news__communications-share:after {
  content: '5';
}

.news__communications-comments:before {
  background-image: url(../img/reviews/comment.png);
}

.news__communications-comments:after {
  content: '3';
}

.news__user {
  margin-bottom: 20px;
  display: flex;
  align-items: center;
}

.news__block {
  text-transform: uppercase;
}

.project {
  margin-top: 100px;
}

.project__content {
  text-align: center;
}

.project__list {
  display: flex;
  justify-content: space-between;
  margin-top: 50px;
}

.project__block {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-transform: uppercase;
  font-weight: 300;
}

.search {
  outline: none;
  border: 1px solid #ccc;
  border-radius: 2px;
  padding: 5px 0 5px 5px;
  max-width: 100%;
}

.search:hover,
.search:focus {
  border-color: black;
}

.services {
  margin-top: 100px;
}

.services__content {
  text-align: center;
}

.services__list {
  margin-top: 100px;
  display: flex;
  justify-content: space-between;
}

.services__block {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-transform: uppercase;
  font-weight: 300;
}

.services__block .block__icon {
  width: 122px;
  height: 120px;
  text-align: center;
}

.slider {
  position: relative;
}

.slider__wrapp {
  overflow: hidden;
}

.slider__list {
  display: flex;
  transition: .5s;
}

.slider__item {
  flex: 1;
}

.slider__descr {
  display: inline-block;
  text-align: center;
  font-weight: 300;
  text-transform: uppercase;
  padding: 0 100px;
}

.slider__title {
  margin-bottom: 7px;
  font-size: 68px;
  color: #ffffff;
}

.slider__subtitle {
  margin-bottom: 10px;
  font-size: 41px;
  font-weight: 400;
  color: #373645;
}

.slider__info {
  margin-bottom: 25px;
  color: #6f9ccb;
}

.slider__page {
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  transform: translateY(-50%);
  display: flex;
  justify-content: space-between;
}

.slider__page-link {
  display: inline-block;
  height: 22px;
  width: 15px;
  background-repeat: no-repeat;
  background-position: center center;
  border: 1px solid #fff;
  padding: 25px;
}

.slider__page-link:hover {
  background-color: #e9e9d9;
}

.slider__page-link--prev,
.slider__page-link--next {
  content: '';
  background-image: url(../img/arrow-link.png);
}

.slider__page-link--next {
  transform: rotate(180deg);
}

.socials__list {
  display: flex;
}

.socials__item {
  margin-right: 10px;
  width: 25px;
  height: 25px;
  background-color: blue;
}

.socials__item:last-child {
  margin-right: 0;
}

.socials__link {
  display: block;
  text-align: center;
  line-height: 25px;
}

.team {
  margin-top: 100px;
}

.team__content {
  text-align: center;
}

.team__list {
  display: flex;
  justify-content: space-between;
  margin-top: 50px;
}

.team__block {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-transform: uppercase;
  font-weight: 300;
}

.team__block .block__button {
  margin-bottom: 20px;
}

.user {
  font-size: 12px;
  font-weight: 300;
  text-transform: uppercase;
}

.user__avatar {
  width: 78px;
  height: 78px;
  margin-right: 10px;
}

.user__avatar-img {
  max-width: 100%;
  max-height: 100%;
}

.user__name {
  color: #373645;
  margin-right: 15px;
}

.user__position {
  color: #6f9ccb;
}

/*base styles*/

* {
  box-sizing: border-box;
  /*transition: .5s;*/
}

html,
body {
  padding: 0;
  margin: 0;
  height: 100%;
  width: 100%;
  font-family: 'Lato';
  font-size: 14px;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
  color: #fff;
}

.wrapper {
  position: relative;
  min-height: 100%;
  width: 100%;
  overflow: hidden;
  color: black;
}

.section {
  height: 100%;
  width: 100%;
}

.container {
  width: 1250px;
  max-width: 100%;
  height: 100%;
  margin: 0 auto;
  /*@include tablets{
            padding: 0 5%;
        }*/
}

.button {
  outline: none;
  border: 1px solid transparent;
  background-color: #6e9bca;
  padding: 10px 5px;
  text-transform: uppercase;
  color: #fff;
  border-radius: 4px;
  transition: .5s;
  cursor: pointer;
}

.button:hover {
  background-color: #fff;
  color: #6e9bca;
}

.button--black {
  background-color: #373645;
}

.overflow {
  overflow: hidden;
}

/*base end*/

@media screen and (max-width: 1200px) {
  .burger {
    opacity: 1;
    visibility: visible;
  }

  .footer__list {
    padding: 0 5%;
  }

  .header__menu {
    display: none;
  }

  .header__search {
    width: 35%;
  }

  .header__search .search {
    width: 100%;
  }

  .header__burger {
    display: inline-block;
  }

  .project__block {
    margin-right: 10px;
  }

  .project__block:last-child {
    margin-right: 0;
  }

  .services__block {
    margin-right: 10px;
  }

  .services__block:last-child {
    margin-right: 0;
  }

  .team__block {
    margin-right: 10px;
  }

  .team__block:last-child {
    margin-right: 0;
  }
}

@media screen and (max-width: 992px) {
  .news__date-number {
    font-size: 40px;
  }
}

@media screen and (max-width: 768px) {
  .about__content {
    flex-direction: column;
    text-align: center;
    padding: 15px 5%;
  }

  .clients__list {
    flex-wrap: wrap;
  }

  .clients__item {
    width: 45%;
    display: flex;
    justify-content: center;
  }

  .clients__item:nth-child(even) {
    margin-right: 0;
  }

  .footer__row:first-child {
    padding: 0 5%;
  }

  .footer__list {
    flex-wrap: wrap;
    margin-bottom: -15px;
  }

  .footer__city {
    width: 48%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 15px;
  }

  .footer__city:last-child {
    margin-bottom: 0;
  }

  .header__logo {
    padding: 15px 5px;
  }

  .header__socials {
    display: none;
  }

  .news__list {
    flex-direction: column;
    padding: 15px 10px;
  }

  .news__item {
    width: 100%;
    margin-right: 0;
    margin-bottom: 10px;
  }

  .news__item:last-child {
    margin-bottom: 0;
  }

  .project__list {
    flex-wrap: wrap;
    margin-bottom: -15px;
    padding: 0 5%;
  }

  .project__block {
    width: 48%;
    margin-right: 0;
    margin-bottom: 15px;
  }

  .project__block:last-child {
    margin-bottom: 0;
  }

  .services__list {
    flex-wrap: wrap;
    margin-bottom: -20px;
    padding: 0 5%;
  }

  .services__block {
    width: 48%;
    margin-right: 0;
    margin-bottom: 20px;
    text-align: center;
  }

  .services__block:last-child {
    margin-bottom: 0;
  }

  .slider__descr {
    padding: 0;
  }

  .team__list {
    flex-wrap: wrap;
    margin-bottom: -15px;
    padding: 0 5%;
  }

  .team__block {
    width: 48%;
    margin-right: 0;
    margin-bottom: 15px;
  }

  .team__block:last-child {
    margin-bottom: 0;
  }
}

@media screen and (max-width: 480px) {
  .footer__row:first-child {
    flex-direction: column;
    align-items: center;
  }

  .footer__copyright {
    width: 100%;
    text-align: center;
  }

  .footer .footer__logo {
    margin-bottom: 10px;
  }

  .footer .footer__socials {
    margin-bottom: 10px;
  }

  .header__logo {
    margin-right: 10px;
  }

  .header__search {
    flex: 1;
    margin-right: 60px;
  }

  .header__burger {
    margin-left: 25px;
  }

  .news__date-month {
    font-size: 19px;
  }

  .news__date-number {
    font-size: 30px;
  }

  .project__block {
    width: 100%;
    text-align: center;
  }

  .services__block {
    width: 100%;
  }

  .slider__item {
    display: flex;
    justify-content: center;
  }

  .slider__title {
    font-size: 45px;
  }

  .slider__subtitle {
    font-size: 27px;
  }

  .slider__info {
    padding: 0 10%;
  }

  .team__block {
    text-align: center;
    width: 100%;
  }
}

@media screen and (max-width: 320px) {
  .clients__list {
    flex-direction: column;
  }

  .clients__item {
    flex: 1;
    margin-right: 0;
    margin-bottom: 10px;
  }

  .clients__item:last-child {
    margin-bottom: 0;
  }
}